$black: #000;
$white: #fff;

// общие для всех тем
$header-top-bg: #1D2B47;

$text-main-color: #303030;
$text-second-color: #6D7076;
$text-third-color: #BDBDBD;
$text-white: #ffffff;
$text-blue: #006BD0;

// оранжевая тема
$accent-color: #FF5B14;

*, *:before, *:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  font-family: "Futura", "Arial", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 112.5%;
  color: $text-main-color;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

svg {
  display: block;
  transition: 0.3s ease-in-out;
}

.icon {
  width: 1em;
  height: 1em;
}

ul, ol {
  list-style: none;
}

a {
  color: inherit;
  transition: 0.3s ease-in-out;
  &:hover {
    text-decoration: underline solid transparent;
    transition: 0.3s ease-in-out;
  }
}

button {
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

*:focus {
  outline: none;
}

/*=== GRID ===*/

.wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
}

footer {
  margin-top: auto;
}

.container {
  max-width: 1080px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;

  @media (max-width: 767px) {
    padding: 0 10px;
  }
}

.section {
  padding: 80px 0;

  @media (max-width: 767px) {
    padding: 60px 0;
  }
}

.section + section:not(.section--bg) {
  padding-top: 0;
}

/*=== CONTENT ===*/

h2 {
  margin: 0;
  font-size: 36px;
  line-height: 112.5%;
  font-weight: 600;
  color: $text-main-color;

  @media (max-width: 767px) {
    font-size: 30px;
  }
}

h3 {
  font-size: 22px;
  line-height: 112.5%;
  font-weight: 600;
  color: $text-main-color;
}

.h2 {
  width: 100%;
  max-width: 650px;
  margin: 0 auto 16px;
  color: $text-main-color;
  text-align: center;
}

.h4 {
  font-size: 20px;
  line-height: 22.5px;
  font-weight: 600;
  color: $text-main-color;
}

.subtitle {
  max-width: 650px;
  width: 100%;
  margin: 0 auto 60px;
  font-size: 22px;
  line-height: 25px;
  font-weight: 300;
  text-align: center;

  @media (max-width: 767px) {
    margin-bottom: 45px;
    font-size: 20px;
  }
}

.btn {
  display: inline-block;
  vertical-align: baseline;
  padding: 11px 16px;
  margin: 0;
  font-size: 16px;
  line-height: 18px;
  color: $white;
  font-weight: 500;
  text-decoration: none;
  text-align: center;
  border: 1px solid $accent-color;
  border-radius: 5px;
  background-color: $accent-color;

  &--light {
    background-color: transparent;
  }

  &--white {
    background-color: $white;
    color: $accent-color;

    &:hover,
    &:focus {
      color: $white;
    }
  }

  &:hover,
  &:focus {
    background-color: rgba($accent-color, 0.7);
    border-color: transparent;
    text-decoration: none;
  }
}

img.lazyload {
  opacity: 0;
  transition: all 0.3s ease-in-out;
}

img.lazyloaded {
  opacity: 1;
  transition: all 0.3s ease-in-out;
}

